import useWindowWidth from '@cetus/hooks/src/useWindowWidth'
import { Icon } from '@cetus/ui-kit'
import { cancelBubble } from '@cetus/utils'
import {
  Center,
  HStack,
  HTMLChakraProps,
  PlacementWithLogical,
  Popover,
  PopoverBody,
  PopoverContent,
  PopoverTrigger,
  Portal,
  Text,
  VStack
} from '@chakra-ui/react'

interface TableSortThProps extends HTMLChakraProps<'div'> {
  labelInfo: { label: string; value: string }
  tooltip?: { content: string | React.ReactNode; placement?: PlacementWithLogical; xlinkHref?: any }
  sortRule: string
  sortBy: { label: string; value: string }
  clickSort: (value: any) => void
  width?: string
}
export default function TableSortTh({ labelInfo, tooltip, sortRule = 'desc', sortBy, clickSort, width, ...rest }: TableSortThProps) {
  const { isApp } = useWindowWidth()
  const mergedTooltip: any = {
    content: '',
    placement: 'top',
    xlinkHref: '#icon-icon_tips',
    ...tooltip
  }
  return (
    <HStack w={width ? width : 'unset'} mr="-4px" justify="flex-end" cursor="pointer" gap="0px" {...rest} onClick={() => clickSort(labelInfo)}>
      <Text fontSize="14px">{labelInfo.label}</Text>
      {mergedTooltip?.content && (
        <Popover isLazy trigger={isApp ? 'click' : 'hover'} placement={mergedTooltip?.placement}>
          <PopoverTrigger>
            <Center as="button" onClick={e => cancelBubble(e)}>
              <Icon xlinkHref={mergedTooltip?.xlinkHref} />
            </Center>
          </PopoverTrigger>
          <Portal>
            <PopoverContent w="fit-content" maxW="320px">
              <PopoverBody>
                {typeof mergedTooltip?.content === 'string' ? (
                  <Text fontSize="12px" lineHeight="20px" dangerouslySetInnerHTML={{ __html: mergedTooltip.content }} />
                ) : (
                  <Text fontSize="12px" lineHeight="20px">
                    {mergedTooltip?.content}
                  </Text>
                )}
              </PopoverBody>
            </PopoverContent>
          </Portal>
        </Popover>
      )}
      {!isApp && (
        <VStack h="20px" gap="0">
          <Icon
            xlinkHref="#icon-icon_ascending_nor"
            svgH="10px"
            svgHover="primary"
            svgFill={labelInfo.value == sortBy?.value && sortRule == 'asc' ? 'primary' : ''}
          />
          <Icon
            xlinkHref="#icon-icon_descending_nor"
            svgH="10px"
            svgHover="primary"
            svgFill={labelInfo.value == sortBy?.value && sortRule == 'desc' ? 'primary' : ''}
          />
        </VStack>
      )}
    </HStack>
  )
}
